<template>
    <mu-paper>
        <mu-list textline="two-line">
            <mu-sub-header inset></mu-sub-header>
            <mu-list-item avatar>
                <mu-list-item-action>
                    <mu-badge circle :color="user.statusLED" badge-class="login-status">
                        <mu-avatar>
                            <img src="@/assets/logo.png" alt="">
                        </mu-avatar>
                    </mu-badge>
                </mu-list-item-action>
                <mu-list-item-content>
                    <mu-list-item-title>{{user.name}}</mu-list-item-title>
                    <mu-list-item-sub-title>{{user.status}}</mu-list-item-sub-title>
                </mu-list-item-content>
                <mu-list-item-action>
                    <mu-button v-if="user.shortStatus === ''" @click="userLogout">
                        
                    </mu-button>
                    <mu-button v-else @click="openDialog(login)">
                        
                    </mu-button>
                </mu-list-item-action>
            </mu-list-item>
            <mu-list-item avatar button>
                <mu-list-item-action>
                    <mu-avatar>
                        <mu-icon value="lock"></mu-icon>
                    </mu-avatar>
                </mu-list-item-action>
                <mu-list-item-content>
                    <mu-list-item-title>()</mu-list-item-title>
                </mu-list-item-content>
                <mu-list-item-action>
                    <mu-button icon>
                        <mu-icon value="info"></mu-icon>
                    </mu-button>
                </mu-list-item-action>
            </mu-list-item>
        </mu-list>
        <mu-divider inset></mu-divider>
        <mu-list textline="two-line">
            <mu-sub-header inset></mu-sub-header>
            <mu-list-item avatar button to="/settings/tags">
                <mu-list-item-action>
                    <mu-avatar color="blue">
                        <mu-icon value="assignment"></mu-icon>
                    </mu-avatar>
                </mu-list-item-action>
                <mu-list-item-content>
                    <mu-list-item-title></mu-list-item-title>
                </mu-list-item-content>
            </mu-list-item>
            <!--<mu-list-item avatar button to="/settings/test">-->
                <!--<mu-list-item-action>-->
                    <!--<mu-avatar color="blue">-->
                        <!--<mu-icon value="assignment"></mu-icon>-->
                    <!--</mu-avatar>-->
                <!--</mu-list-item-action>-->
                <!--<mu-list-item-content>-->
                    <!--<mu-list-item-title></mu-list-item-title>-->
                <!--</mu-list-item-content>-->
            <!--</mu-list-item>-->
            <mu-list-item avatar button to="/settings/detail">
                <mu-list-item-action>
                    <mu-avatar color="blue">
                        <mu-icon value="build"></mu-icon>
                    </mu-avatar>
                </mu-list-item-action>
                <mu-list-item-content>
                    <mu-list-item-title></mu-list-item-title>
                </mu-list-item-content>
            </mu-list-item>
            <mu-list-item avatar button>
                <mu-list-item-action>
                    <mu-avatar color="yellow600">
                        <mu-icon value="insert_chart"></mu-icon>
                    </mu-avatar>
                </mu-list-item-action>
                <mu-list-item-content>
                    <mu-list-item-title></mu-list-item-title>
                </mu-list-item-content>
                <mu-list-item-action>
                    <mu-button icon>
                        <mu-icon value="info"></mu-icon>
                    </mu-button>
                </mu-list-item-action>
            </mu-list-item>
        </mu-list>
        <mu-dialog :open.sync="dialogOpen" v-bind="dialog">
            <component :is="dialog.component" :host="_self" v-if="dialog"/>
        </mu-dialog>
        <mu-snackbar position="bottom-end" :color="toast.color" :open.sync="toast.open">
            {{toast.message}}
            <mu-button flat slot="action" color="#fff" @click="toast.open = false"></mu-button>
        </mu-snackbar>
    </mu-paper>
</template>

<script>
	import LoginDialog from "./login-dialog";
	import {mapState} from "vuex";
    import init from '@/lib/init';
    import {io} from '@/lib/io';
    import localconfig from '@/lib/config/local';

	export default {
		name: "settings-manager",
		components: {LoginDialog},
		props: ['st', 'beBind'],
		computed: {
			...mapState({
				user: 'user'
			})
		},
		data() {
			return {
				dialog: null,
				dialogOpen: false,
				login: {
					'title': '',
					width: "600",
					'max-width': '80%',
					'esc-press-close': false,
					'overlay-close': false,
					component: LoginDialog
				},
				toast: {
					open: false,
					color: 'secondary',
					message: ''
				}
			}
		},
		methods: {
			openPaper() {
				this.st.openFullscreenDialog('/settings/tags');
			},
			postToast(opts,autoClose = true) {
				this.toast = Object.assign(this.toast, opts);
				this.toast.open = true;
				if (autoClose) setTimeout(() => {
					this.toast.open = false;
				}, 3000);
			},
			openDialog(config) {
				this.dialogOpen = true;
				this.dialog = config;
			},
			closeDialog() {
				this.dialogOpen = false;
				this.dialog = null;
			},
            async userLogout(){
                let ret = await this.$store.dispatch('logout');
                if (ret.status === 'success') {
                    console.log('set side list', init);
                    this.$store.commit('setSideList', init.body);
                    let item = init.body[0].children[0];
                    let local = await io.fetchObj(item.prop);
                    this.$store.commit('selectCatalog', {
                        prop: item.prop,
                        data: local.data,
                        index: '0|0'});
                    io.save(localconfig.dbname.side_list, {body: init.body});
                }
            }
		},
		mounted() {
			this.$emit('setEnv', {title: ''});
            this.$store.commit('setSync', this);
        },
		beforeRouteLeave(to, from, next) {
			console.log('before route leave', to, this.$route);
			if (to.path === '/')
				this.st.closeFullscreenDialog();
			// 
			//  `this`
			next();
		}
	}
</script>

<style>
    .login-status {
        height: 10px !important;
