<template>
    <mu-container>
        <mu-form ref="form" :model="validateForm">
            <mu-form-item label="" label-float prop="username" :rules="usernameRules">
                <mu-text-field v-model="validateForm.username"
                               prop="username"></mu-text-field>
            </mu-form-item>
            <mu-form-item label="" label-float help-text="620" prop="password" :rules="passwordRules">
                <mu-text-field type="password" v-model="validateForm.password" prop="password"></mu-text-field>
            </mu-form-item>
            <!--<mu-form-item prop="isAgree" :rules="argeeRules">-->
                <!--<mu-checkbox label="" v-model="validateForm.isAgree"></mu-checkbox>-->
            <!--</mu-form-item>-->
            <mu-flex justify-content="around" align-items="center" align-content="around">
                <mu-button color="primary" @click="submit">
                    <label style="margin-left: 15px"></label>
                    <mu-circular-progress :style="{opacity: loading.login?1:0}" style="display: flex;" color="#fff"
                                          class="loading-circle"
                                          :size="18"/>
                </mu-button>
                <mu-button color="success" @click="signUp">
                    <label style="margin-left: 15px"></label>
                    <mu-circular-progress :style="{opacity: loading.sign?1:0}" style="display: flex;" color="#fff"
                                          class="loading-circle"
                                          :size="18"/>
                </mu-button>
                <mu-button @click="cancel"></mu-button>
            </mu-flex>
        </mu-form>
    </mu-container>
</template>
<script>
    import {io} from '@/lib/io';

    export default {
        name: "login-dialog",
        props: ['host'],
        data() {
            return {
                loading: {
                    login: false,
                    sign: false
                },
                usernameRules: [
                    {validate: (val) => !!val, message: ''},
                    {validate: (val) => val.length >= 6 && val.length <= 30, message: '630'},
                    {validate: emailValidate, message: ''}
                ],
                passwordRules: [
                    {validate: (val) => !!val, message: ''},
                    {validate: (val) => val.length >= 6 && val.length <= 20, message: '620'}
                ],
                argeeRules: [{validate: (val) => !!val, message: ''}],
                validateForm: {
                    username: '',
                    password: '',
                    // isAgree: false
                }
            }
        },
        methods: {
            async submit() {
                let pass = await this.$refs.form.validate();
                if (pass) {
                    this.loading.login = true;
                    let ret = await this.$store.dispatch('login', this.validateForm);
                    if (ret.status === 'success') {
                        this.host.postToast({message: '', color: 'success'});
                        this.host.closeDialog();
                    } else if (ret.status === 'error') {
                        switch (ret.msg) {
                            case 'not found':
                                this.host.postToast({message: ''});
                                break;
                            case 'wrong password':
                                this.host.postToast({message: ''});
                                break;
                        }
                    }
                    this.loading.login = false;
                }
            },
            async signUp() {
                let pass = await this.$refs.form.validate();
                if (pass) {
                    this.loading.sign = true;
                    let ret = await this.$store.dispatch('signUp', this.validateForm);
                    if (ret.status === 'success') {
                        this.host.postToast({message: ''},false);
                    }
                    this.loading.sign = false;
                }
            },
            clear() {
                this.$refs.form.clear();
                this.validateForm = {
                    username: '',
                    password: '',
                    isAgree: false
                };
            },
            cancel() {
                this.host.closeDialog();
            }
        }
    };

    function emailValidate(val) {
        return val.match(/\w[-\w.+]*@([A-Za-z0-9][-A-Za-z0-9]+\.)+[A-Za-z]{2,14}/g);
    }

    function passwdValidate(val) {
        return val.match(/^[a-zA-Z0-9]+$/g);
    }
</script>
<style>

    .loading-circle {
        justify-content: center;
        align-items: center;
        left: 7px;
        transition: opacity 300ms ease-in-out;
    }
</style>